import React, { useState } from "react";
function InputArea(props) {
  const [text1, changeText1] = useState("");
  const [text2, changeText2] = useState("");
  const [clicked, changeclick] = useState(false);
  function setText1(event) {
    changeText1(event.target.value);
  }
  function setText2(event) {
    changeText2(event.target.value);
  }
  function setClick() {
    changeclick(true);
  }
  return (
    <div className="container-fluid col-6 text-center inputArea">
      <input
        onClick={setClick}
        onChange={setText1}
        type="text"
        placeholder="Title"
        value={text1}
      />
      <br />
      <input
        style={{ display: !clicked ? "none" : "block" }}
        onChange={setText2}
        type="text"
        placeholder="Description"
        value={text2}
      />
      <br />
      <button
        className="btn btn-primary"
        onClick={() => {
          props.fun(text1, text2);
          changeText1("");
          changeText2("");
        }}
      >
        Add
      </button>
    </div>
  );
}
export default InputArea;
