import React, { useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
// import notes from "../notes";
import Input from "./Input";
function App() {
  const [notes, changeNotes] = useState([]);
  function addNote(text1, text2) {
    changeNotes((prevNotes) => {
      return [...prevNotes, [text1, text2]];
    });
  }
  function deleteNote(id) {
    console.log("clciked");
    changeNotes((prevNotes) => {
      return prevNotes.filter((item, index) => {
        return index !== id;
      });
    });
    // console.log(id);
  }
  return (
    <div>
      <Header />
      <Input fun={addNote}></Input>
      <div className="container-fluid Notes">
        {notes.map((note, index) => {
          console.log(index);
          return (
            <Note
              key={index}
              index={index}
              fun2={deleteNote}
              title={note[0]}
              description={note[1]}
            />
          );
        })}
      </div>
      <Footer />
    </div>
  );
}

export default App;
